.section-contents {
    background-image: url("../../public/bg_desert_h.png");
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center;    
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.section-name {
    margin: 0;
    color: #FFF;    
    font-weight: 700;
    font-size: 72px;
}

.subsection-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    height: 2rem;
    position: relative;
    width: 100%;
}

.subsection-contents {
    position: absolute;
    font-size: 1.5rem;
    color: #FFF;
    transition: all 0.4s ease;
    font-weight: 700
}

@media (min-width: 768px) {
    .section-contents {
	background-image: url("../../public/bg_desert.png");
	background-repeat:no-repeat;
	background-size:cover;
	background-position: center;
	align-items: flex-start;	
	box-sizing: border-box;
	margin-right: auto;
	padding-left: 6rem;
    }

    .section-name {
	font-size: 96px;	
    }
    
    .subsection-container {
	display: block;
    }
    
    .subsection-contents {
    }
}

