.contact-contents {
    position: absolute;
    height: 100vh;
    top: 205vh;
    padding-left: 4rem;
    padding-right: 4rem;
    user-select: none;
    user-select: none;
    background-color: #FFF;
    z-index: 20;
}

.contact-header {
    font-weight: 700;
    display: flex;
    justify-content: center;
    font-size: 18px;
    margin-bottom: 1rem;
}

.contact-body {
    max-width: 64rem;
}


.form {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 3fr 1fr;
    font-size: 24px;
    justify-content: center;
    width: 600px;
}

.form-name-key, .form-mail-key, .form-message-key {
    font-size: 18px;
    display: flex;
    justify-content: left;
    align-items: center;    
}

.form-name-input, .form-mail-input, form-message-input {
    display: flex;
    justify-content: left;
    align-items: center;

}

.form-name-input-area, .form-mail-input-area, .form-message-input-area {
    width: 240px
}

.form-message-input-area {
    height: 120px;
    resize: none;
}

.form-submit-button {
    width: 120px;
}

@media(min-width: 768px) {
    .form {
	display: grid;
	grid-template-rows: 1fr 1fr 3fr 1fr;
	grid-template-columns: 2fr 4fr;    
	font-size: 24px;
	width: 600px;
    }
    
    .contact-contents {
	position: absolute;
	height: 100vh;
	top: 212vh;
        padding-left: 6rem;
        padding-right: 6rem;
    }
}


