.header-container {
    display: none;    
}

.header-button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background: rgba(0,0,0,0.4);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
}


@media (min-width: 768px) {
    .header-container {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 31vw;
	display: flex;
	justify-content: flex-start;
	gap: 1rem;
	padding: 1rem;
	background: rgba(255, 255, 255, 0);
	z-index: 10;
	padding-left: 6rem;
    }
}


