.about-contents {
    position: absolute;
    height: 100vh;
    top: 105vh;
    padding-left: 4rem;
    padding-right: 4rem;
    user-select: none;
}

.about-header {
    font-size: 18px;    
    font-weight: 700;
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}


.detail {
    margin-bottom: 1rem;
}

.keyword {
    font-weight: 700;
    font-size: 14px;
}

.summary {
    font-weight: 300;
    font-size: 12px;

}

@media(min-width: 768px) {
    .about-header {
	margin-bottom: 2rem;
    }

    .detail {
	margin-bottom: 2rem;
    }
    
    .about-contents {
	position: absolute;
	height: 100vh;
	top: 110vh;
	max-width: 64rem;
	padding-left: 6rem;
	padding-right: 6rem;	
    }
    .keyword {
	font-weight: 700;
	font-size: 20px;
    }

    .summary {
	font-weight: 300;
	font-size: 15px;
	
    }    
}


@media(min-width: 1080px) {
    .keyword {
	font-weight: 700;
	font-size: 24px;
    }

    .summary {
	font-weight: 300;
	font-size: 18px;
	
    }        
}

