.button-container {
    position: absolute;
    top: 0;
    right: 1rem;
    width: 80px;
    height: 80px;
    z-index: 50;
}

.up-button, .down-button {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: rgba(200,200,200,0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

}


.up-button:hover, .down-button:hover {
    background-color: rgba(200,200,200,0.4);
}

@media (min-width: 768px) {
    .button-container {
	display: none;	
    }
}
